import Vue from 'vue'
import Router from 'vue-router'
//about us
import policyDetail from "@/components/policy";
import landing from "@/components/landing";

Vue.use(Router)

export default new Router({
    routes: [
        //aboutUs
        {
            path: "/policy/:name",
            name: "policyDetail",
            component: policyDetail,
            meta: {
                title: "FasDo Policy"
            },
        },
        //landing
        {
            path: "/",
            name: "landing",
            component: landing,
            meta: {
                title: "FasDo"
            },
        },
    ]
})


