<template>
  <div>
    <div class="row grad m-0">
      <div class="col-lg-12 text-center p-0">
        <div class="ds-heading">
          <h2 class="font-weight-bolder text-white">{{heading}}</h2>
          <span class="text-white">FasDo.</span>
        </div>
      </div>
    </div>
    <div class="row mt-4 mb-4 ml-0 mr-0">
      <div class="col-lg-2"></div>
      <div class="col-lg-8 col-md-12">
        <div v-if="loading" class="text-center">
          <LoaderGif />
        </div>
        <div class="mt-2 mb-2 aboutUs" v-html="content" v-else></div>
      </div>
      <div class="col-lg-2"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { domain } from "@/store/variable";
import LoaderGif from "./LoaderGif.vue";

export default {
  name: "policyDetail",
  components: {
    LoaderGif
  },

  data() {
    return {
      heading: null,
      content: null,
      loading: false
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "aboutUs";
    axios
      .get(url, {
        params: {
          name: to.params.name
        }
      })
      .then(res => {
        res.data.forEach(doc => {
          next(vm => {
            if (to.params.name == "termsAndCondition") {
              vm.heading = "Terms and Conditions";
            } else if (to.params.name == "privacyPolicy") {
              vm.heading = "Privacy Policy";
            } else if (to.params.name == "aboutUs") {
              vm.heading = "About US";
            }
            vm.content = doc;
          });
        });
      })
      .catch(err => {
        console.log(err.message);
      });
  },

  watch: {
    $route: "fetchContent"
  },

  methods: {
    fetchContent() {
      this.loading = true;
      if (this.$route.params.name == "termsAndCondition") {
        this.heading = "Terms and Conditions";
      } else if (this.$route.params.name == "privacyPolicy") {
        this.heading = "Privacy Policy";
      } else if (this.$route.params.name == "aboutUs") {
        this.heading = "About US";
      }

      let url = domain + "aboutUs";
      axios
        .get(url, {
          params: {
            name: this.$route.params.name
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.content = doc;
            this.loading = false;
          });
        })
        .catch(err => {
          console.log(err.message);
          this.loading = false;
        });
    }
  }
};
</script>

<style scoped>
.grad {
  background-image: linear-gradient(to right, #bdc3c7, #2c3e50);
}

.ds-heading {
  padding-top: 4.5em;
  padding-bottom: 4.5em;
}

.aboutUs h1,
.aboutUs h2,
.aboutUs h3,
.aboutUs h4,
.aboutUs h5,
.aboutUs h6 {
  font-size: 1.1 rem;
  font-weight: normal;
  color: #2c211e;
}

.aboutUs strong {
  color: #2c211e;
}

.aboutUs {
  font-family: "Poppins";
  font-size: 1 rem;
  line-height: 1 em;
  color: #606c71;
}
</style>



